<template>
    <d2-container>
        <!-- 常见问题反馈管理 -->
        <!-- header -->
        <template slot="header">
            <el-row>
                <el-col :span="24">
                    <!-- <el-autocomplete class="inline-input" v-model="state1" :fetch-suggestions="querySearch" placeholder="请输入内容"></el-autocomplete> -->
                    <el-alert title="常见问题反馈管理" type="info" :closable="false">
                    </el-alert>
                </el-col>

                <!--   <el-col :span="5">
                        <el-button type="primary" plain>查询</el-button>
                        <el-button type="primary" plain @click="add_good">添加</el-button>
                    </el-col> -->
            </el-row>
        </template>
        <el-table :data="tableData" border style="width: 100%">

            <el-table-column type="index" width="50">
            </el-table-column>
            <el-table-column prop="master_name" label="提出人">
            </el-table-column>
            <el-table-column prop="master_phone" label="提出人电话">
            </el-table-column>
            <el-table-column prop="question_title" label="常见问题反馈">
            </el-table-column>
            <el-table-column prop="question_desc" label="常见问题反馈答案">
            </el-table-column>
            <el-table-column prop="created_at" label="设置时间">
            </el-table-column>
            <el-table-column prop="updated_at" label="更新时间">
            </el-table-column>
            <el-table-column label="操作" width="280">
                <template scope="val">
                    <!-- <el-button type="primary" plain @click="edit_type(val.row)">编辑</el-button> -->
                    <!-- <el-button type="primary" plain>修改</el-button> -->
                    <el-button type="danger" plain @click="del_val(val.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- footer -->
        <el-row type="flex" justify="space-between" align="middle" style="height: 80px;">
            <el-col :span="24">
                <el-pagination layout="prev, pager, next" @current-change="page" :page-size="10" :total="checked" style="float: right;">
                </el-pagination>
            </el-col>
        </el-row>


        <!-- 添加常见问题 -->
        <!-- <el-dialog :title="num" :visible.sync="centerDialogVisible" width="30%" center :before-close="clear">
                <el-form ref="form" :model="form" label-width="100px">
                    <el-form-item label="常见问题标题">
                        <el-input v-model="form.question_title" placeholder="请输入常见问题标题"></el-input>
                    </el-form-item>
                    <el-form-item label="常见问题回答">
                        <el-input v-model="form.question_answer" placeholder="请输入常见问题回答"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="clear">取 消</el-button>
                    <el-button type="primary" @click="onSubmit" v-if="num!=='编辑常见问题'">确 定</el-button>
                    <el-button type="primary" @click="onSubmit_type" v-if="num=='编辑常见问题'">确 定</el-button>
                </span>
            </el-dialog> -->

    </d2-container>

</template>

<script>
    import { add_goods_type, show_goods_type, edit_goods_type, del_goods_type } from '../../plugin/axios/api.js';
    import { add_question, del_question, edit_question, show_question, show_new_question, del_new_question } from '../../plugin/axios/api.js';
    import { Myerror } from '../../plugin/axios/er.js';
    import qs from 'qs';
    export default {
        name: 'Feedback_home',
        data() {
            return {
                num: '',
                value3: '',
                state1: '',
                checked: 0,
                tableData: [],
                dialogVisible: false,
                pages: 1,
                centerDialogVisible: false,
                form: {
                    question_title: '',
                    question_answer: '',
                },
            }
        },
        methods: {
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                // 调用 callback 返回建议列表的数据
                cb(results);
            },
            createFilter(queryString) {
                return (restaurant) => {
                    return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
                };
            },
            // add_good() {//添加常见问题
            //     this.num = "添加常见问题";
            //     this.centerDialogVisible = true;
            // },
            // onSubmit() {//添加常见问题
            //     let par = qs.stringify(this.form)
            //     add_question(par).then(res => {
            //         this.msg(res)
            //     })
            // },
            // edit_type(a) {//编辑常见问题
            //     this.num = "编辑常见问题";
            //     this.form = {
            //         question_title: a.question_title,
            //         question_answer: a.question_answer,
            //         id: a.id
            //     }
            //     this.centerDialogVisible = true;
            // },
            // onSubmit_type() {//编辑常见问题
            //     let par = {
            //         question_title: this.form.question_title,
            //         question_answer: this.form.question_answer,
            //     }
            //     let id = this.form.id;
            //     edit_question(par, id).then(res => {
            //         this.msg(res)
            //     })
            // },
            del_val(a) {//删除常见问题
                console.log(a);
                del_new_question(a.id).then(res => {
                    this.msg(res)
                })
            },
            page(a) {
                console.log(a);
                this.pages = a;
                this.init_goods()
            },
            init_goods() {
                let par = {
                    page: this.pages,
                    limit: 10
                }
                show_new_question(par).then(res => {
                    // console.log(res);

                    this.tableData = res.data.data;
                    this.checked = res.data.paginate.total;
                })
            },
            msg(res) {//返回参数处理
                if (res.code) {
                    this.$message({
                        message: res.message,
                        center: true,
                        type: 'success'
                    });
                    this.clear()
                    this.init_goods()

                    return
                }
                Myerror(res, this)
            },
            clear() {
                this.centerDialogVisible = false;
                this.form = {
                    question_title: '',
                    question_answer: '',
                }

            }
        },
        mounted() {
            this.init_goods()
        }
    }
</script>
